import './index.scss';
import { useTranslation } from 'react-i18next';
import BaseSectionTitle from '../../BaseSectionTitle';
import videoImg from '../../../assets/images/about-video.png';
import play from '../../../assets/images/play.svg';

function AboutVideo() {
  const { t } = useTranslation();

  return (
    <div className="about__video">
      <div className="about__video__inner">
        <div className="about__video__right">
          <div className="about__video__right-inner">
            <img src={videoImg} alt="" />
          </div>
          <button className="about__video__btn">
            <img src={play} alt="" />
          </button>
        </div>
        <div className="about__video__left">
          <div className="about__video__content">
            <BaseSectionTitle titleSmall="aboutUs.videoBlog" titleBig="aboutUs.companyVideo" />
            <p className="text-md">{ t('aboutUs.videoT1') }</p>
            <div>
              <span className="text-md">{ t('aboutUs.videoT2') }</span>
              <span className="text-sm pn-bold">{ t('aboutUs.videoT3') }</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default AboutVideo;